body {
  background-color: #ececec;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

nav {
  font-weight: 900;
  font-size: 1.2em;
  padding: .5em 1em;
  margin-bottom: 0;
}

.profile-image {
  height: 70vh;
  width: 100vw;
  overflow: hidden;
}

.profile-image img {
  height: 70vh;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.profile-details {
  padding: .5em 1em;
}

.blurb {
  font-family: Arial;
  font-size: 14px;
  line-height: 1.7em;
}

.name-lrg {
  font-size: 50px;
  font-weight: 900;
  margin: 0;
}

svg {
  margin-right: .5em;
}

a, .fa-spinner {
  color: #636363;
}

a:hover {
  color: #0075ff
}

.social-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 1em;
  margin-top: 1em;
}

@media (min-width: 800px) {
  .container {
    max-width: 800px;
    margin: auto;
  }
  .profile-image {
    width: auto;
  }
}



